        <p style="height:150px; line-height:200px; font-size:24px; color:#F00;text-align: center;">
          数据审核无误，请点击“确认申报”进行申报！</p>
        <button @click="declare" class="uk-button uk-button-primary"><i class="uk-icon-save"></i> 确认申报</button>
      </div>
      <div class="blB">
        <!--增值税申报表审核结果-->
        <p style="font-size:24px; font-weight:bold; color:#999; height:150px; line-height:200px;">增值税申报表审核结果</p>
        <p style="font-size:20px; font-weight:bold; color:#F00; width:50%; text-align:left; margin:0px auto;">
          申报数据已上报，请确认已签订三方协议的缴税账户余额是否充足，否则无法网上申报自动扣款。</p>
        <p style="font-size:20px; font-weight:bold; color:#F00; width:50%; text-align:left; margin:0px auto;">
          请您关注申报状态及税款扣转的银行账号余额变动情况。</p>
        <p style="font-size:20px; font-weight:bold; color:#F00; width:50%; text-align:left; margin:0px auto;">
          如自动扣款未成功，请于本月申报期最后一日下午到国税主管税务机关缴纳税款！</p>
        <p style="font-size:20px; font-weight:bold; color:#F00; width:50%; text-align:left; margin:0px auto;">
          申报期最后一日中午12时，网上办税平台停止自动扣款处理功能。</p>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        name: 35,
        ZZS: this.$store.state.isConfirmed.ZZS
      }
    },
    methods: {
      declare: function () {
        J('.blA').css('display', 'none')
        J('.blB').css('display', 'block')
        // sessionStorage.setItem("vatDeclare", 1);  //添加到sessionStorage
        this.$store.commit('saveConfirm', { name: 'ZZS', data: true })
      }
    }
  }
</script>

<style scoped>
  .p2 {
    text-align: center;
  }

  .blB {
    display: none;
  }
</style>



